export const StatusMap = {
    'opened': '',
    'inProgress': ' ',
    'complete': '',
    'testing': '',
};

export const RankMap = {
    'low': '',
    'medium': '',
    'high': '',
};

export const TaskMap = {
    'task': '',
    'bug': ''
}

export const closeDropdown = (evt) => {
    evt.target.classList.remove('capet-up');
}

export const toggleDropdown = (evt) => {
    evt.target.classList.toggle('capet-up');
}